.watch {
  border: 10px dashed #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.watch__dot {
  z-index: 1;
  background: #f28c28;
  border-radius: 50%;
  width: 3vw;
  height: 3vw;
  position: absolute;
}

.watch__quick-hand {
  transform-origin: bottom;
  background: #568203;
  width: 1vw;
  height: 21vw;
  margin-bottom: 21vw;
  animation: 5s linear infinite clock;
  position: absolute;
}

.watch__slow-hand {
  transform-origin: bottom;
  background: #2a52be;
  width: 1.5vw;
  height: 10vw;
  margin-bottom: 10vw;
  animation: 60s linear infinite clock;
  position: absolute;
}

@keyframes clock {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

